body {
    background-image: url("background.png");
    background-color: #cccccc;
    background-position: center 0;
    background-repeat: no-repeat;
    font-family:Arial, Helvetica, sans-serif;
   }

h1 {
    padding-top: 170px;
    text-align: center;
    color:rgb(255, 237, 134);
    font-size: 100px;
}

h2 {
    text-align: center;
